body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  width: 100%;
  background: white;
  padding: 20px 40px;
  box-sizing: border-box;
  border-bottom: 1px solid #FAFBFC; }

header a {
  font-weight: 400;
  color: black;
  text-decoration: none;
  transition: .2s all; }

.left {
  font-weight: 600; }

.right {
  float: right;
  color: #999;
  padding-left: 20px;
  font-size: 14px; }

.right:hover {
  color: black; }

.banner {
  background: #FAFBFC;
  padding: 15px;
  font-size: 14px;
  text-align: center;
  -webkit-animation-name: appear;
          animation-name: appear;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s; }

@-webkit-keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }
  .banner .bold {
    font-weight: 600; }
  .banner .light-link {
    color: #999;
    transition: 0.2s all;
    text-decoration: none; }
  .banner .link-link:hover {
    color: blue; }

.mainstage {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 400px;
  flex-direction: column; }
  .mainstage .lead {
    font-size: 45px;
    font-weight: 600;
    margin-bottom: 20px;
    -webkit-animation-name: lead-animation;
            animation-name: lead-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s; }

@-webkit-keyframes lead-animation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes lead-animation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px); }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }
  .mainstage .sublead {
    opacity: 0;
    font-size: 20px;
    color: #999;
    -webkit-animation-name: lead-animation;
            animation-name: lead-animation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-delay: .3s;
            animation-delay: .3s; }

.carousel {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 120px 40px;
  grid-gap: 40px; }
  .carousel .block {
    background: black;
    height: 250px; }
  .carousel .title {
    font-weight: 400;
    font-size: 22px;
    margin: 15px 0; }
  .carousel .subtitle {
    color: #999;
    font-weight: 400; }
  .carousel .learn {
    margin-top: 10px;
    color: black;
    font-weight: 500; }

.newsletter {
  height: 200px;
  background: #FAFBFC;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea; }

